function getVocabs(level) {
    return vocabEntry.filter(entry => entry.JLPT === level && entry.kanji !== '')
}

function checkKanji(kanji) {
    return vocabEntry.find(entry => entry.kanji.includes(kanji));
}

export {getVocabs, checkKanji};